.card{

    background-color: #fee57e;
    display: flex;
    flex-direction: column;
    min-width:15rem;
    align-items: center;
    border: 3px solid black;
    border-radius: 1rem;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 26rem;
  

    .card__image{

        padding:5%;
        height: 12rem;;


    }

    .card__name{

        background-color: #ff96b4;
        width: 100%;
        text-align:center;
        margin-bottom:2rem;


    }

    .card__tagline{

        font-style: italic;
        font-size: 1rem;
        font-weight: 500;
        margin-top:0;
        margin-bottom:0;
        text-align:center;

    }


    .card__info{
        display: flex;
        gap: 2rem;
        align-items: baseline;
        margin-top:0;

    .card__abv{

        font-size:0.8rem;
        font-weight:500;
        font-style: italic;


    }

    .card__ph{

        font-size:0.8rem;
        font-weight:500;
        font-style: italic;



    }

    .card__firstbrewed{


        font-style: italic;
        font-weight:500;


    }
}

    .card__description{


    }

    .card__ingredients-container{


    }
}


.card:hover{
    background: rgba(0,0,0,.5);
    opacity: 1;

}

.card::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}
